.generated {
    @include container;
    margin-bottom: 128px;
    position: relative;
    z-index: 1;

    &__title {
        color: $text;
        font-size: 32px;
        font-weight: 600;
        margin-bottom: 96px;
        position: relative;
        width: fit-content;
        padding: 0 4px 12px;

        &::before {
            content: "";
            height: 7px;
            background: $backlight;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            border-radius: 60px;
        }
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 92px;
        margin-bottom: 160px;
    }

    &__item {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 47px;
        border: 2px solid $extra;
        overflow: hidden;

        &-img {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            background: $product-bg;

            img {
                width: 340px;
                max-height: 305px;
                height: auto;
            }
        }

        &-descr {
            width: 100%;
            height: 100%;
            padding: 24px 48px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
        }

        &-price {
            color: $text;
            font-family: "Montserrat";
            font-size: 32px;
            font-weight: 600;
            margin-bottom: 48px;
        }

        &-btns {
            display: flex;
            align-items: center;

            button {
                color: $text;
                font-family: "Montserrat";
                font-size: 16px;
                font-weight: 500;
                padding: 14px 25px;
                border-radius: 100px;
                background: transparent;
                border: 2px solid $green;
                cursor: pointer;
                transition: 0.2s;

                &:hover {
                    opacity: 0.8;
                }

                &:first-of-type {
                    margin-right: 32px;
                }
            }
        }
    }

    &__btn-try {
        background: $backlight;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        font-family: "Montserrat";
        font-size: 36px;
        font-weight: 500;
        padding: 24px 48px;
        border: 2px solid $extra;
        border-radius: 100px;
        display: flex;
        width: fit-content;
        margin: 0 auto;
        transition: .2s linear;

        &:hover {
            opacity: .8;
        }
    }
}
