* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

button {
    border: none;
    outline: none;
}

.wrapper {
    background: $main;
    font-family: "Montserrat";
    background-image: url("../images/glass.png");
    *::before,
    *::after {
        font-family: "FontAwesome";
    }
    z-index: 1;
    position: relative;
}

.icon {
    &-plus::before {
        content: "\2b";
        color: $text;
        font-weight: 900;
    }

    &-search::before {
        content: "\f002";
        color: $text;
        font-weight: 900;
    }

    &-telegram::before {
        content: "\f2c6";
        color: $text;
        font-weight: 500;
    }

    &-instagram::before {
        content: "\f16d";
        color: $text;
        font-weight: 500;
    }

    &-youtube::before {
        content: "\f167";
        color: $text;
        font-weight: 500;
        background: transparent;
    }

    &-arrow_right::before {
        content: "\f178";
        color: $green;
        font-weight: 900;
    }

    &-arrow_left::before {
        content: "\f177";
        color: $green;
        font-weight: 900;
    }
}

.page-title {
    font-family: "NeueMachina";
    font-size: 178px;
    font-weight: 400;
    color: $text;
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 64px auto;
    align-items: flex-end;


    span {
        background: $backlight;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        // margin-right: min(64px, 5%);
        margin-bottom: -50px;
        align-self: flex-start;
    }

    img {
        width: 154px;
        height: 154px;
        position: absolute;
        left: 30%;
        top: 35%;
    }
}

.backlights {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    
    &-blur {
        width: min(823px, 45%);
        height: min(380px, 20rem);
        background: $backlight;
        border-radius: 823px;
        filter: blur(60px);
        position: absolute;
        opacity: .25;
    }

    &-blur:first-child {
        right: 15vh;
        top: 20vh;
    }

    &-blur:nth-child(2) {
        left: 0;
        top: 80vh;
    }
    
    &-blur:last-child {
        right: 5vh;
        top: 135vh;
    }
}

%button {
    color: $text;
    font-size: 20px;
    font-family: "Montserrat";
    font-weight: 500;
    border: 1px solid;
    border-radius: 100px;
    background: transparent;
    padding: 24px 48px;
    transition: 0.2s;
    cursor: pointer;

    &:hover {
        opacity: 0.75;
    }
}

%footer {
    &-title {
        color: $text;
        font-family: "Montserrat";
        font-size: 32px;
        font-weight: 600;
    }

    &-text {
        color: $extra;
        font-family: "Montserrat";
        font-size: 24px;
        font-weight: 400;
    }
}

%products {
    &-price {
        color: $text;
        font-family: "Montserrat";
        font-size: 32px;
        font-weight: 600;
    }

    &-title {
        color: $text;
        font-family: "Montserrat";
        font-size: 24px;
        font-weight: 400;
    }
}
