.detailed {
    @include container;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-block: 64px 128px;

    &__img {
        border-radius: 64px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: $product-bg;
        padding: 24px 64px;
        width: fit-content;

        img {
            height: 525px;
            width: 525px;
            object-fit: cover;
        }
    }

    &__descr {
        display: flex;
        flex-direction: column;
        width: 40%;
        height: auto;

        &-title {
            color: $text;
            font-family: "Montserrat";
            font-size: 48px;
            font-weight: 600;
            margin-bottom: 32px;
        }

        &-size {
            color: $detailed;
            font-family: "Montserrat";
            font-size: 24px;
            font-weight: 400;
            margin-bottom: 24px;
        }

        &-list {
            display: flex;
            flex-direction: column;
            margin-bottom: 32px;
        }

        &-item {
            color: $detailed;
            font-family: "Montserrat";
            font-size: 24px;
            font-weight: 400;

            &:not(:last-of-type) {
                margin-bottom: 8px;
            }
        }

        &-price {
            color: $text;
            font-family: "Montserrat";
            font-size: 36px;
            font-weight: 600;
            margin-bottom: 48px;
        }

        &-delivery {
            color: $detailed;
            font-family: "Montserrat";
            font-size: 16px;
            font-weight: 400;
        }

        &-buttons {
            display: flex;
            align-items: center;

            button:first-child {
                background: $green;
                color: $btn-text;
                margin-right: 45px;
                border-color: $green;
            }

            button:last-child {
                border-color: $blue;
            }

            button {
                @extend %button;
            }
        }
    }
}
