.printed {
    @include container;
    margin-bottom: 128px;
    position: relative;
    z-index: 1;

    &__title {
        color: $text;
        font-size: 32px;
        font-weight: 600;
        margin-bottom: 96px;
        position: relative;
        width: fit-content;
        padding: 0 4px 12px;

        &::before {
            content: "";
            height: 7px;
            background: $backlight;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            border-radius: 60px;
        }
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 92px;
    }

    &__item {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 47px;
        border: 2px solid $extra;
        overflow: hidden;

        &-img {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            background: $product-bg;

            img {
                width: 340px;
                max-height: 305px;
                height: auto;
            }
        }

        &-descr {
            padding-block: 24px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
        }

        &-title {
            color: $text;
            font-size: 32px;
            font-weight: 600;
            margin-bottom: 64px;
        }

        &-btn {
            padding: 24px 48px;
            font-family: "Montserrat";
            border-radius: 100px;
            background: $backlight;
            font-weight: 500;
            color: $btn-text;
            font-size: 16px;
            cursor: pointer;
            transition: .2s;

            &:hover {
                opacity: .8;
            }
        }
    }
}
